import React from 'react'
import { Helmet } from 'react-helmet'

import { graphql } from 'gatsby'

import { flattenFileNode } from '../helpers'

import Layout from '../components/layout'
import ArticlePreview from '../components/article-preview'
import MyJumbotron from '../components/jumbotron'
import CardGrid from '../components/card-grid'

export default indexPage

function indexPage({ data, location }) {
  // aplat des objets
  const nodes = data.allMarkdownRemark.edges.map(flattenFileNode)

  return (
    <Layout location={location} data={data}>
      <Helmet>
        <meta name="robots" content="index, follow" />
        <meta name="msvalidate.01" content="23C4486B3F6CCCF85A73AA582BA8DF9E" />
        <title>
          lyceum un site open-source de cours et d&apos;exercices pour le lycée
        </title>
        <meta
          name="keywords"
          content="cours, lycée, informatique, snt, nsi, lyceum"
        />
        <meta
          name="description"
          content="Un site open-source pour les lycéens."
        />
        <meta
          name="classification"
          content="lycée cours physique chimie informatique"
        />
        <meta name="author" content="Benjamin Abel" />
      </Helmet>
      <MyJumbotron>
        <h1>Derniers cours</h1>
      </MyJumbotron>
      <CardGrid>
        {nodes.map((node) => (
          <ArticlePreview
            key={node.id}
            node={node}
            dataShowClasse
            dataShowMatter
            dataShowPart
            dataShowModifiedTime
          />
        ))}
      </CardGrid>
    </Layout>
  )
}

export const query = graphql`
  query HomeQuery {
    __typename
    allMarkdownRemark(
      sort: { fields: { modifiedDate: DESC } }
      filter: { fields: { type: { eq: "course" } } }
      limit: 6
    ) {
      edges {
        node {
          id
          frontmatter {
            tags
          }
          fields {
            modifiedDate
            slug
            classe
            matter
            nPart
            part
            nChapter
            chapter
          }
        }
      }
    }
  }
`
